.tabs {
	margin: 40px 0
}

.tabs .tabs {
	display: none !important
}

.tabs .tab-panel {
	background: #f5f5f1;
	padding: 16px 16px 24px
}

@media(max-width: 600px) {
	.tabs .tab-panel {
		padding:24px
