		padding:20px 24px
	}
}

.tabs .tab-.landing>.tab-title a:hover {
	color: #e50914
}

.tabs .tab-.landing>.tab-title a::before,.tabs .tab-.landing>.tab-title a::after {
	background: #e50914;
	content: "";
