.article-feedback {
	border-top: 1px solid #d5d4d1;
	margin-top: 24px;
	padding: 24px 0
}

.article-feedback .feedback-header {
	margin: 0 0 16px
}

.article-feedback .unable-to-respond {
	font-style: italic
}

.article-feedback .feedback-prompt::after {
	clear: both;
	content: "";
	display: table
}

.article-feedback .feedback-prompt .feedback-header {
	float: left;
	margin: 0
}

.article-feedback .feedback-prompt .prompt-buttons .h-btn {
	margin: 0 0 0 16px;
	min-width: 0
}

.article-feedback .problems {
	display: block
}

.article-feedback .problems::after {
	clear: both;
	content: "";
	display: table
}

.article-feedback .problems .problem {
	display: block;
	margin: 0 0 16px;
	position: relative
}

.article-feedback .problems .problem .form-group:last-of-type {
	margin: 0
}

.article-feedback .problems .problem .Collapsible__contentInner {
	padding: 16px 0 24px 28px
}

@media(max-width: 600px) {
	.article-feedback .problems .problem {
		min-height:48px;
		padding-left: 48px
	}

	.article-feedback .problems .problem .Collapsible__contentInner {
		padding-left: 0
	}
}

.article-feedback .problems .radio {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	background: #fff;
	border: 1px solid #8b8a88;
	border-radius: 50%;
	display: block;
	display: inline-block;
	height: 20px;
	margin: 0 8px 0 0;
	padding: 0;
	position: relative;
	vertical-align: middle;
	width: 20px
}

.article-feedback .problems .radio::after {
	background: #e50914;
	border-radius: 50%;
	content: "";
	height: 12px;
	left: 50%;
	margin: -6px 0 0 -6px;
	opacity: 0;
	position: absolute;
	top: 50%;
	transition: opacity 150ms ease;
	width: 12px;
	z-index: 1
}

@media(max-width: 600px) {
	.article-feedback .problems .radio {
		height:32px;
		left: 0;
		position: absolute;
		top: 8px;
		width: 32px
	}

	.article-feedback .problems .radio::after {
		height: 20px;
		margin: -10px 0 0 -10px;
		width: 20px
	}
}

.article-feedback .problems .problem-trigger {
	cursor: pointer;
	display: block
}

.article-feedback .problems .problem-trigger.faded {
	color: #8b8a88
}

.article-feedback .problems .problem-trigger.faded .radio {
	border-color: #d5d4d1
}

.article-feedback .problems .problem-trigger.is-open {
	color: #221f1f
}

.article-feedback .problems .problem-trigger.is-open .radio::after {
	opacity: 1
}

.article-feedback .problems .problem-trigger.is-closed+.Collapsible__contentOuter {
	user-select: none;
	visibility: hidden
}

